import {Box, Center, Flex, Image, SimpleGrid, Text} from "@chakra-ui/react";
import React from "react";

import {Link} from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUser as farUser, faComments as farComments } from "@fortawesome/free-solid-svg-icons";
import { faUser, faComments } from "@fortawesome/free-regular-svg-icons"

export default function Navigator(props){
    let highlight1 = false;
    let highlight2 = false;
    if(props.index == 1)
        highlight1 = true;
    else if (props.index == 2)
        highlight2 = true;

    return(
        <SimpleGrid columns={2} spacing={1} gap='0px' w='full' h='59px' color='white' >
            <Link to='/Main'>
                <Box as='button' textAlign='center' w='full' h='full' aria-selected={highlight1}>
                    <Center display='grid'>
                        <SimpleGrid columns={1} spacing={2} gap='0.1em' placeItems='center' >
                            <FontAwesomeIcon icon={highlight1 ? farUser : faUser} color='gray' />
                            <Text fontSize='sm' textColor='black'>
                                대화상대
                            </Text>
                        </SimpleGrid>
                    </Center>
                </Box>
            </Link>
            <Link to='/ChatList'>
                <Box as='button' textAlign='center' w='full' h='full' aria-selected={highlight2}>
                    <Center display='grid'>
                        <SimpleGrid columns={1} spacing={2} gap='0.1em' placeItems='center' >
                            <FontAwesomeIcon icon={highlight2 ? farComments : faComments} color='gray' />
                            <Text fontSize='sm' textColor='black'>
                                대화
                            </Text>
                        </SimpleGrid>
                    </Center>
                </Box>
            </Link>
        </SimpleGrid>
    );
}