
const initialState = [];

const chatReducer = (state = initialState, action) => {
    switch (action.type) {
        case "ADD_CHAT_ITEM":
            return [...state, action.payload];
        case "DELETE_CHAT_ITEM":
            return [...action.payload];
        case "CLEAR_CHAT_ITEM":
            return [];
        default:
            return state;
    }
}

export default chatReducer;