import React from 'react';
import {
    Box,
    InputGroup,
    Wrap,
    WrapItem,
    InputLeftElement,
    Input,
    Center,
    Image,
    IconButton
} from '@chakra-ui/react';

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSignOutAlt } from "@fortawesome/free-solid-svg-icons";
import { Search2Icon, SearchIcon } from '@chakra-ui/icons'

export default function MainHeader(){

    function logout(e){
        window.location.href = process.env.PUBLIC_URL + '/#/Login'
    }

    return (
        <Box h='100px' bg='#00BCD5' zIndex='5'>
            <Wrap px='10px' py='5px' w='100%' spacing='10px'>
                <WrapItem w='100%'>
                    <Box w='100px' h='30px'>
                        <Image src={process.env.PUBLIC_URL + '/resources/icon/top_logo.png'} alt='제품 로고' />
                    </Box>
                    <Box flex='1'></Box>
                    <Box as='button'
                         w='30px'
                         h='30px'
                         backgroundColor='#00a6bc'
                         borderRadius='0.3rem'
                         display='inline'
                         lineHeight='initial'
                         onClick={logout}
                    >
                        <FontAwesomeIcon icon={faSignOutAlt} color='white' />
                    </Box>
                </WrapItem>
                <WrapItem w='100%'>
                    <Center w='100%' >
                        <InputGroup>
                            <InputLeftElement
                                pointerEvents='none'
                                children={<Search2Icon color='gray.300' />}
                            />
                            <Input bg='white' type='text' placeholder='이름, 부서명, 전화번호, 이메일, 담당업무' />
                            <IconButton aria-label='Search' icon={<SearchIcon />} ml='5px'/>
                        </InputGroup>
                    </Center>
                </WrapItem>
            </Wrap>
        </Box>
    );
}