

export const addChat = (item) => {
    return {
        type: "ADD_CHAT_ITEM",
        payload: item
    }
};

export const deleteChat = (item) => {
    return {
        type:"DELETE_CHAT_ITEM",
        payload: item
    }
};

export const clearChat = () => {
    return {
        type:"CLEAR_CHAT_ITEM"
    }
};