import React, {useEffect, useState} from "react";
import {
    Box,
    Flex,
    FormControl,
    FormLabel,
    IconButton,
    Image,
    Input,
    InputGroup,
    InputLeftElement,
    InputRightElement,
    Spinner,
    Switch,
    Text,
    Wrap,
    WrapItem
} from "@chakra-ui/react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUser, faKey, faArrowRight } from "@fortawesome/free-solid-svg-icons";

export default function Chat(props){
    const [isLoading, setLoading] = useState(false);
    const [isSaveID, setSaveID] = useState(false);
    const [isAutoLogin, setAutoLogin] = useState(false);

    useEffect(() => {
    }, []);

    function onKeyDownEvent(e) {
        if(e.keyCode === 13 && e.shiftKey === false) {
            login(e);
            e.preventDefault();
        }
    }

    function login(e){
        setLoading(true)
        setTimeout(() =>
            window.location.href = process.env.PUBLIC_URL + '/#/Main'
        , 1000)
    }

    function manageCheckbox(e){
        const targetName = e.target.name;
        const targetCheck = e.target.checked;

        if(targetName == 'autoLogin' && targetCheck){
            setSaveID(true)
            setAutoLogin(true)
        } else if (targetName == 'autoLogin' && !targetCheck) {
            setSaveID(true)
            setAutoLogin(false)
        } else if (targetName == 'saveID' && targetCheck) {
            setSaveID(true)
            setAutoLogin(false)
        } else{
            setSaveID(false)
            setAutoLogin(false)
        }
    }
    return (
        <Flex
            backgroundColor='#00BCD5'
            flexDirection='column'
            w='full'
            h='full'
        >
            <Box flex='0.1'></Box>
            <Box
                flex='0.3'
                verticalAlign='center'
                textAlign='center'
                ml='auto'
                mr='auto'
            >
                <Image
                    boxSize='250px'
                    objectFit='scale-down'
                    align='center'
                    src={process.env.PUBLIC_URL + '/resources/icon/main_logo.png'}
                >

                </Image>
            </Box>

            <Box flex='0.1'></Box>

            <Box
                flex='0.3'
                verticalAlign='center'
                textAlign='center'
                ml='auto'
                mr='auto'
            >
                <FormControl>
                    <InputGroup>
                        <InputLeftElement
                            pointerEvents='none'
                            children={<FontAwesomeIcon icon={faUser} color='gray' />}
                        />
                        <Input type='text' id='userID' name='userID' placeholder='아이디' backgroundColor='#fff' borderRadius='5px 5px 0px 0px' tabIndex='1'/>
                    </InputGroup>
                    <InputGroup>
                        <InputLeftElement
                            pointerEvents='none'
                            children={<FontAwesomeIcon icon={faKey} color='gray' />}
                        />
                        <InputRightElement children={
                                isLoading ?
                                    <Spinner
                                        thickness='4px'
                                        speed='0.4s'
                                        emptyColor='gray.200'
                                        color='cyan.500'
                                        size='md'
                                    /> :
                                    <IconButton
                                        size='xs'
                                        mt='0px'
                                        variant='outline'
                                        colorScheme='gray'
                                        borderRadius='50%'
                                        icon={<FontAwesomeIcon icon={faArrowRight} color='gray' />}
                                        onClick={login}
                                    />
                            }
                        />
                        <Input type='password' id='password' name='password' placeholder='비밀번호' onKeyDown={onKeyDownEvent} backgroundColor='#fff' borderRadius='0px 0px 5px 5px'  tabIndex='2'/>
                    </InputGroup>
                    <Wrap m='5px 0px 0px 5px'>
                        <WrapItem w='100%'>
                            <Switch colorScheme='gray' id='saveID' className='saveID' name='saveID' isChecked={isSaveID} onChange={manageCheckbox} aria-label='asdsadafas'></Switch>
                            <FormLabel htmlFor='saveID' mb='0'>
                                <Text m='1px 0px 0px 5px' textColor='white' fontSize='0.8rem'>아이디 저장</Text>
                            </FormLabel>
                        </WrapItem>
                        <WrapItem w='100%'>
                            <Switch colorScheme='gray' id='autoLogin' className='autoLogin' name='autoLogin' isChecked={isAutoLogin} onChange={manageCheckbox}></Switch>
                            <FormLabel htmlFor='autoLogin' mb='0'>
                                <Text m='1px 0px 0px 5px' textColor='white' fontSize='0.8rem'>자동 로그인</Text>
                            </FormLabel>
                        </WrapItem>
                    </Wrap>
                </FormControl>
            </Box>

            <Box flex='0.2'></Box>
        </Flex>
    );
}